@import 'normalize';
@import 'variables';

body {
    background: $blue;
    color: white;
    font-family: 'Helvetica Neue', sans-serif;
    font-size: 1.2rem;
    height: 100vh;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    align-items: center;
    justify-content: center;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.4rem;
}

h4 {
    font-weight: normal;
}

input, select, button {
    border: none;
    border-radius: 3px;
    background: white;
}

input[type=text], input[type=number], select {
    height: 40px;
    width: 300px;
    padding: 10px;
    color: $grey;
}

input[type=submit] {
    height: 40px;
    width: 180px;
    color: $blue;
    font-weight: bolder;
    transition: 0.1s all ease;
    &:hover {
        background: darken($blue, 10%);
        color: white;
        position: relative;
        top: 1px;
    }
}

button {
    height: 40px;
    padding: 0 30px;
    color: $blue;
    font-weight: bolder;
    margin: 20px auto;
    &:hover {
        background: darken($blue, 10%);
        color: white;
        position: relative;
        top: 1px;
    }
}

.logo {
    height: 100px;
}

.car-steps {
    font-weight: bold;
}

#landing {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
}

.home-divider {
    border: none;
    border-left: 1px solid rgba(255,255,255,0.5);
    height: 60vh;
    margin: 0 80px;
}

.home-left {
    text-align: right;
    width: 50%;
}

.home-right {
    width: 30%;
}

.home-tag {
    margin: 20px 0;
    font-size: 1.3rem;
}

.p2 {
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    flex-wrap: wrap;
}

.p2-half {
flex: 50%;
// width: 50%;
}

.btn-next {
    // flex: 100%;

}

@import 'media_queries';
